import { fontFamily } from '@mui/system';
import React from 'react'
import './index.css'
import cv from './CV Daniel Maruahal 2022.pdf'
import Profile from '../profile'

import {
    BrowserRouter as Router,
    Routes,
    Route,
    Link
} from "react-router-dom";
import { HashLink } from 'react-router-hash-link'
import Button from '@mui/material/Button'
import BottomNavigation from '@mui/material/BottomNavigation';
import BottomNavigationAction from '@mui/material/BottomNavigationAction';
import KeyboardDoubleArrowDownIcon from '@mui/icons-material/KeyboardDoubleArrowDown';
import DownloadingIcon from '@mui/icons-material/Downloading';
import FactCheckIcon from '@mui/icons-material/FactCheck';

const Header = () => {
    const [value, setValue] = React.useState(0)
    return (
        <div className='headerall'>
            <div className='container'>
                <h1
                    className='headertext1'
                    style={{
                        color: "white"
                    }}
                >
                    Daniel Maruahal
                </h1>
                <p
                    className='headertext2'
                    style={{
                        color: "white",
                        fontFamily: "sans-serif"
                    }}>
                    Interactive curriculum vitae
                </p>
                <Button variant="contained" startIcon={<FactCheckIcon />} onClick={() => window.open(cv)}>View Daniel's CV</Button>
                <div className='hometoprofile hometoprofilemobile'>
                    <Router>
                        <div>
                            <BottomNavigation
                                value={value}
                                onChange={(event, newValue) => {
                                    setValue(newValue);
                                }}
                                style={{ background: 'none' }}
                            >
                                <BottomNavigationAction icon={<KeyboardDoubleArrowDownIcon className='updown'/>} component={HashLink} smooth to="#profile" />
                            </BottomNavigation>
                            <Routes>
                                <Route path="#profile" element={<Profile />} />
                            </Routes>
                        </div>
                    </Router>
                </div>
            </div>

        </div>
    )
};

export default Header;