import React, { useState, useRef } from 'react'
import Header from '../header'
import Profile from '../profile'
import Education from '../education'
import Experience from '../experience'
import Skills from '../skills'
import Projects from '../projects'
import Contact from '../contact'
import './index.css'
import {
    BrowserRouter as Router,
    Routes,
    Route,
    Link
} from "react-router-dom";
import Box from '@mui/material/Box';
import List from '@mui/material/List';
import { HashLink } from 'react-router-hash-link'
import Button from '@mui/material/Button'
import BottomNavigation from '@mui/material/BottomNavigation';
import BottomNavigationAction from '@mui/material/BottomNavigationAction';
import KeyboardDoubleArrowDownIcon from '@mui/icons-material/KeyboardDoubleArrowDown';
import DownloadingIcon from '@mui/icons-material/Downloading';
import FactCheckIcon from '@mui/icons-material/FactCheck';
import HomeIcon from '@mui/icons-material/Home';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import SchoolIcon from '@mui/icons-material/School';
import ContactMailIcon from '@mui/icons-material/ContactMail';
import StarsIcon from '@mui/icons-material/Stars';
import PsychologyIcon from '@mui/icons-material/Psychology';
import AccountTreeIcon from '@mui/icons-material/AccountTree';
import { useInView } from "react-intersection-observer";
import { useEffect } from 'react'


const ButtonDrawer = () => {
    const [value, setValue] = React.useState(0)

    // SIDEBAR SCROLL HIGHLIGHT: ADAPTATION TO REACT from https://codepen.io/Web_Cifar/pen/LYRBbVE
    const sections = document.querySelectorAll('section');
    const navLi = document.querySelectorAll('nav ul li');
    const [color, setColor] = useState(false)

    const changeColor = () => {
        let current = '';
        sections.forEach((section) => {
            const sectionTop = section.offsetTop;
            const sectionHeight = section.clientHeight;
            if (window.pageYOffset >= sectionTop - sectionHeight / 3) {
                current = section.getAttribute('id')
            }
        })
        console.log(current) // return id of current section name
        console.log(current) // return id of current section name
        

        navLi.forEach((li) => {
            // console.log(li.className)
            // console.log(li.id)
            // console.log(li.id.includes(current))
            if (li.id.includes(current)) {
                li.classList.add('singlenavactive')

            } else {
                li.classList.remove('singlenavactive')
            }
        });
    }

    useEffect(() => {
        window.addEventListener('scroll', changeColor)
        // return () => {
        //     window.removeEventListener('scroll', changeColor)
        // }
    }, [])

    return <>
        {/* FOR DESKTOP */}
        <div className='desktop'>
            <Router sx={{ display: 'flex', position: 'sticky' }} >
                <div className='naviall'>
                    <nav className='nav'>
                        <ul className='containernav'>
                            <li className='singlenav' id='header1' style={{ borderRadius: '10px 10px 0 0' }}>
                                <HashLink smooth to="#header" style={{ textDecoration: 'none' }}>
                                    <Button variant="contained" startIcon={<HomeIcon />} style={{ background: 'none', boxShadow: 'none', width: '150px', justifyContent: 'flex-start' }}>HOME</Button>
                                </HashLink>
                            </li>
                            <li className='singlenav' id='profile1'>
                                <HashLink smooth to="#profile" style={{ textDecoration: 'none' }}>
                                    <Button variant="contained" startIcon={<AccountCircleIcon />} style={{ background: 'none', boxShadow: 'none', width: '150px', justifyContent: 'flex-start' }}>Profile</Button>
                                </HashLink>
                            </li>
                            {/* <li className={ `singlenavactive ${color && 'singlenav'}` } > */}
                            {/* <li className={color ? 'singlenavactive' : 'singlenav'} id='education'> */}
                            <li className='singlenav' id='education1'>
                                <HashLink smooth to="#education" style={{ textDecoration: 'none' }}>
                                    <Button variant="contained" startIcon={<SchoolIcon />} style={{ background: 'none', boxShadow: 'none', width: '150px', justifyContent: 'flex-start' }}>Education</Button>
                                </HashLink>
                            </li>
                            <li className='singlenav' id='experience1'>
                                <HashLink smooth to="#experience" style={{ textDecoration: 'none' }}>
                                    <Button variant="contained" startIcon={<StarsIcon />} style={{ background: 'none', boxShadow: 'none', width: '150px', justifyContent: 'flex-start' }}>Experiences</Button>
                                </HashLink>
                            </li>
                            <li className='singlenav' id='skills1'>
                                <HashLink smooth to="#skills" style={{ textDecoration: 'none' }}>
                                    <Button variant="contained" startIcon={<PsychologyIcon />} style={{ background: 'none', boxShadow: 'none', width: '150px', alignItems: 'right', justifyContent: 'flex-start' }}>Skills</Button>
                                </HashLink>
                            </li>
                            <li className='singlenav' id='projects1'>
                                <HashLink smooth to="#projects" style={{ textDecoration: 'none' }}>
                                    <Button variant="contained" startIcon={<AccountTreeIcon />} style={{ background: 'none', boxShadow: 'none', width: '150px', alignItems: 'right', justifyContent: 'flex-start' }}>Projects</Button>
                                </HashLink>
                            </li>
                            <li className='singlenav' id='contact1' style={{ borderRadius: '0 0 10px 10px' }}>
                                <HashLink smooth to="#contact" style={{ textDecoration: 'none' }}>
                                    <Button variant="contained" startIcon={<ContactMailIcon />} style={{ background: 'none', boxShadow: 'none', width: '150px', textAlign: 'right', justifyContent: 'flex-start' }}>Contact</Button>
                                </HashLink>
                            </li>
                        </ul>
                    </nav>
                    <Routes>
                        <Route path="#header" element={<Header />} />
                        <Route path="#profile" element={<Profile />} />
                        <Route path="#education" element={<Education />} />
                        <Route path="#experience" element={<Experience />} />
                        <Route path="#skills" element={<Skills />} />
                        <Route path="#projects" element={<Projects />} />
                        <Route path="#contact" element={<Contact />} />
                    </Routes>
                </div>
            </Router>
        </div>
    </>
}
export default ButtonDrawer