import React from 'react'
import './index.css'
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import FacebookIcon from '@mui/icons-material/Facebook';
import GoogleIcon from '@mui/icons-material/Google';
import AlternateEmailIcon from '@mui/icons-material/AlternateEmail';
import InstagramIcon from '@mui/icons-material/Instagram';
import { grey } from '@mui/material/colors'

const Contact = () => {
    return <>
        <div className='contactcontainer contactcontainermobile'>
            <h1 className='title3'>Contact</h1>
            <p className='title3'>"Trust in the Lord always, for the Lord God is an everlasting Rock."<br></br><i>Isaiah 26:4</i></p>
            <p style={{textAlign: 'center', fontSize: '18px', color: 'white', padding: '20px'}}><i>This project is built on custom made ReactJS and CSS frameworks.</i></p>
            <div className='socmedcontainer' >
                <a href="https://www.linkedin.com/in/daniel-maruahal-24170444/" target="_blank" style={{ textDecoration: 'none', color:'white' }} className='socmedcontainer2 linkedin'><div><LinkedInIcon sx={{ color: grey[50] }}/><br></br>LINKEDIN</div></a>
                <a href="https://www.facebook.com/daniel.m.simarmata/" target="_blank" style={{ textDecoration: 'none', color:'white' }} className='socmedcontainer2 facebook'><div><FacebookIcon sx={{ color: grey[50] }}/><br></br>FACEBOOK</div></a>
                <a href="https://www.instagram.com/danielmaruahal/" target="_blank" style={{ textDecoration: 'none', color:'white' }} className='socmedcontainer2 instagram'><div><InstagramIcon sx={{ color: grey[50] }}/><br></br>INSTAGRAM</div></a>
                <a href="mailto: danielmaruahal@gmail.com" target="_blank" style={{ textDecoration: 'none', color:'white' }} className='socmedcontainer2 gmail'><div><GoogleIcon sx={{ color: grey[50] }}/><br></br>GMAIL</div></a>
                <a href="mailto: daniel.maruahal@tum.de" target="_blank" style={{ textDecoration: 'none', color:'white' }} className='socmedcontainer2 tumemail'><div><AlternateEmailIcon sx={{ color: grey[50] }}/><br></br>TUM EMAIL</div></a>
            </div>
            <p className='title3' >Copyright © Daniel Maruahal 2022. All rights reserved.</p>
        </div>
    </>
}

export default Contact