import React, { useState, useRef } from 'react'
import Header from '../header'
import Profile from '../profile'
import Education from '../education'
import Experience from '../experience'
import Skills from '../skills'
import Projects from '../projects'
import Contact from '../contact'
import './index.css'
import {
    BrowserRouter as Router,
    Routes,
    Route,
    Link,
    NavLink
} from "react-router-dom";
import Box from '@mui/material/Box';
import List from '@mui/material/List';
import { HashLink } from 'react-router-hash-link'
import Button from '@mui/material/Button'
import BottomNavigation from '@mui/material/BottomNavigation';
import BottomNavigationAction from '@mui/material/BottomNavigationAction';
import KeyboardDoubleArrowDownIcon from '@mui/icons-material/KeyboardDoubleArrowDown';
import DownloadingIcon from '@mui/icons-material/Downloading';
import FactCheckIcon from '@mui/icons-material/FactCheck';
import HomeIcon from '@mui/icons-material/Home';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import SchoolIcon from '@mui/icons-material/School';
import ContactMailIcon from '@mui/icons-material/ContactMail';
import StarsIcon from '@mui/icons-material/Stars';
import PsychologyIcon from '@mui/icons-material/Psychology';
import AccountTreeIcon from '@mui/icons-material/AccountTree';
import { useInView } from "react-intersection-observer";
import { useEffect } from 'react'
import { Navbar, Nav, Container } from 'react-bootstrap'

import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';

import Divider from '@mui/material/Divider';
import ListItem from '@mui/material/ListItem';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import InboxIcon from '@mui/icons-material/MoveToInbox';
import MailIcon from '@mui/icons-material/Mail';
import Drawer from '@mui/material/Drawer';

const ButtonDrawerMobile = () => {
    const [value, setValue] = React.useState(0)

    // SIDEBAR SCROLL HIGHLIGHT: ADAPTATION TO REACT from https://codepen.io/Web_Cifar/pen/LYRBbVE
    const sections = document.querySelectorAll('section');
    const navLi = document.querySelectorAll('nav ul li');
    const [color, setColor] = useState(false)

    const changeColor = () => {
        let current = '';
        sections.forEach((section) => {
            const sectionTop = section.offsetTop;
            const sectionHeight = section.clientHeight;
            if (window.pageYOffset >= sectionTop - sectionHeight / 3) {
                current = section.getAttribute('id')
            }
        })
        console.log(current) // return id of current section name
        console.log(current) // return id of current section name
        

        navLi.forEach((li) => {
            // console.log(li.className)
            // console.log(li.id)
            // console.log(li.id.includes(current))
            if (li.id.includes(current)) {
                li.classList.add('singlenavactivemobile')

            } else {
                li.classList.remove('singlenavactivemobile')
            }
        });
    }

    useEffect(() => {
        window.addEventListener('scroll', changeColor)
        // return () => {
        //     window.removeEventListener('scroll', changeColor)
        // }
    }, [])

    return <>
        {/* FOR MOBILE */}
        <div className='mobile outer'>
            <Router>
                <div className='naviallmobile'>
                    <nav className='navmobile'>
                        <ul className='containernavmobile'>
                            {/* <li className='topblocknavi mobile'>Navi</li> */}
                            <li className='singlenavmobile' id='header1'>
                                <HashLink smooth to="#header" style={{ textDecoration: 'none' }}>
                                    <IconButton variant="contained" className='buttonmobile' style={{ background: 'none', boxShadow: 'none', color: 'white' }}><HomeIcon /></IconButton>
                                </HashLink>
                            </li>
                            <li className='singlenavmobile' id='profile1'>
                                <HashLink smooth to="#profile" style={{ textDecoration: 'none' }}>
                                    <IconButton variant="contained" className='buttonmobile' style={{ background: 'none', boxShadow: 'none', color: 'white' }}><AccountCircleIcon /></IconButton>
                                </HashLink>
                            </li>
                            <li className='singlenavmobile' id='education1'>
                                <HashLink smooth to="#education" style={{ textDecoration: 'none' }}>
                                    <IconButton variant="contained" className='buttonmobile' style={{ background: 'none', boxShadow: 'none', color: 'white' }}><SchoolIcon /></IconButton>
                                </HashLink>
                            </li>
                            <li className='singlenavmobile' id='experience1'>
                                <HashLink smooth to="#experience" style={{ textDecoration: 'none' }}>
                                    <IconButton variant="contained" className='buttonmobile' style={{ background: 'none', boxShadow: 'none', color: 'white' }}><StarsIcon /></IconButton>
                                </HashLink>
                            </li>
                            <li className='singlenavmobile' id='skills1'>
                                <HashLink smooth to="#skills" style={{ textDecoration: 'none' }}>
                                    <IconButton variant="contained" className='buttonmobile' style={{ background: 'none', boxShadow: 'none', color: 'white' }}><PsychologyIcon /></IconButton>
                                </HashLink>
                            </li>
                            <li className='singlenavmobile' id='projects1'>
                                <HashLink smooth to="#projects" style={{ textDecoration: 'none' }}>
                                    <IconButton variant="contained" className='buttonmobile' style={{ background: 'none', boxShadow: 'none', color: 'white' }}><AccountTreeIcon /></IconButton>
                                </HashLink>
                            </li>
                            <li className='singlenavmobile' id='contact1'>
                                <HashLink smooth to="#contact" style={{ textDecoration: 'none' }}>
                                    <IconButton variant="contained" className='buttonmobile' style={{ background: 'none', boxShadow: 'none', color: 'white' }}><ContactMailIcon /></IconButton>
                                </HashLink>
                            </li>
                        </ul>
                    </nav>
                    <Routes>
                        <Route path="#header" element={<Header />} />
                        <Route path="#profile" element={<Profile />} />
                        <Route path="#education" element={<Education />} />
                        <Route path="#experience" element={<Experience />} />
                        <Route path="#skills" element={<Skills />} />
                        <Route path="#projects" element={<Projects />} />
                        <Route path="#contact" element={<Contact />} />
                    </Routes>
                </div>
            </Router>
        </div>

    </>
}
export default ButtonDrawerMobile